body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f9fa;
  color: #333;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.app-header {
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
}

.app-header h1 {
  font-size: 1.25rem;
  margin: 0;
  background: linear-gradient(90deg, #4285f4, #9b72cb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.sidebar {
  width: 300px;
  background-color: #f1f3f4;
  padding: 1.5rem;
  border-right: 1px solid #e0e0e0;
  display: flex;
  flex-direction: column;
}

.upload-section h3 {
  font-size: 0.9rem;
  text-transform: uppercase;
  color: #5f6368;
  margin-bottom: 1rem;
}

.file-input {
  margin-bottom: 1rem;
  width: 100%;
}

.upload-btn {
  width: 100%;
  padding: 0.5rem;
  background-color: #1a73e8;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}

.upload-btn:disabled {
  background-color: #dadce0;
  cursor: not-allowed;
}

.file-badge {
  margin-top: 1rem;
  background: white;
  padding: 0.5rem;
  border-radius: 8px;
  border: 1px solid #dadce0;
  font-size: 0.9rem;
  word-break: break-all;
}

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: white;
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
}

.messages-list {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.empty-state {
  text-align: center;
  color: #5f6368;
  margin-top: 20%;
}

.message {
  display: flex;
  flex-direction: column;
  max-width: 80%;
}

.message.user {
  align-self: flex-end;
  align-items: flex-end;
}

.message.bot {
  align-self: flex-start;
  align-items: flex-start;
}

.message-content {
  padding: 1rem;
  border-radius: 12px;
  line-height: 1.5;
  font-size: 1rem;
}

.message.user .message-content {
  background-color: #e8f0fe;
  color: #1a73e8;
  border-top-right-radius: 2px;
}

.message.bot .message-content {
  background-color: #f1f3f4;
  color: #202124;
  border-top-left-radius: 2px;
}

.message p {
  margin: 0.5rem 0;
}

.message p:first-child {
  margin-top: 0;
}

.message p:last-child {
  margin-bottom: 0;
}

.input-area {
  padding: 1.5rem;
  border-top: 1px solid #e0e0e0;
  display: flex;
  gap: 1rem;
  align-items: flex-end;
}

textarea {
  flex: 1;
  border: 1px solid #dadce0;
  border-radius: 8px;
  padding: 1rem;
  font-family: inherit;
  font-size: 1rem;
  resize: none;
  height: 24px;
  max-height: 150px;
  outline: none;
  transition: border-color 0.2s;
}

textarea:focus {
  border-color: #1a73e8;
}

.input-area button {
  background-color: #1a73e8;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
}

.input-area button:disabled {
  background-color: #dadce0;
  cursor: default;
}